html {
  scroll-behavior: smooth;
}

body {
  margin: 0px;
}

.nav-bar {
  height: 90px;
}

.nav-left{
  float:left;
  padding-left: 20px;
}

.nav-right {
  float:right;
  padding-top: 20px;
  padding-right: 20px;
}

.nav-right li{
  padding:12px 20px;
  list-style:none;
  font-size:16px;
  color:white;
}

.nav-right ul {
  list-style-type: none;
  margin: 0px;
  padding: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: inherit;
  font-family: Verdana, sans-serif;
}

a {
  color: #2f4454;
  text-decoration: none;
}

main{
  text-align : center;
  font-size: 14px;
}

.main-content {
  display: grid;
  font-family: Verdana, sans-serif;
}

.introduction {
  color: #f9fbfd;
  padding-top: 7%;
  padding-bottom: 7%;
  font-size: 30px;
  letter-spacing: 0.3em;
  padding-left: 15%;
  padding-right: 15%;
  background:linear-gradient(0deg, rgba(73, 73, 73, 0.6), rgba(73, 73, 73, 0.6)), url("/src/assets/img/pexels-jakub-novacek-924824.jpg");
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.introduction img {
  width: 25%;
}

.fade-in-section {
  opacity: 0;
  transform: translateY(20vh);
  visibility: hidden;
  transition: opacity 0.6s ease-out, transform 0.5s ease-out;
  will-change: opacity, visibility;
}
.fade-in-section.is-visible {
  opacity: 1;
  transform: none;
  visibility: visible;
}

.about {
  padding-top: 5%;
  padding-bottom: 5%;
  background-color: #eae7dc;
  padding-left: 20%;
  padding-right: 20%;
  font-size: 17px;
  color: black;
}

.experience {
  font-size: 17px;
  color: #f9fbfd;
  display: grid;
  padding-top: 5%;
  padding-bottom: 5%;
  padding-left: 20%;
  padding-right: 20%;
  min-height: 500px;
  background:linear-gradient(0deg, rgba(73, 73, 73, 0.8), rgba(73, 73, 73, 0.8)), url("/src/assets/img/pexels-cameron-casey-1722183.jpg");
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.content-0 img {
  width: 70%;
}

.content-1 img {
  width: 80%;
}

.content-2 img {
  width: 80%;
}

#content-3-img {
  width: 90%;
}

.content-5 img {
  width: 80%;
}

.experience ul {
  list-style-type: none;
  padding: 0px;
  display: flex;
  justify-content: center;
}

.content-1 li {
    display: inline-block;
    padding: 5px 0px;
    width: 5.5%;
}

.content-2 li {
  display: inline-block;
  padding: 5px 0px;
  width: 10%;
}

#project a {
  color: #ac3b61;
  font-weight: bold;
}

#project a:hover {
  color: #b1667f;
}

#loader-wrapper {
  position: fixed;
  width: 100%;
  height: 100%;
  background: #222222;
}

#myProgress {
  position:fixed;
  left: 47%;
  top: 50%;
  width: 100px;
  border-radius: 25px 25px;
  background-color: #ddd;
}

#myBar {
  width: 1%;
  height: 5px;
  border-radius: 25px 25px;
  background-color: #ac3b61;
}

.animate-bottom {
  position: relative;
  -webkit-animation-name: animatebottom;
  -webkit-animation-duration: 1s;
  animation-name: animatebottom;
  animation-duration: 1s
}

.pure-material-button-contained {
    position: relative;
    display: inline-block;
    box-sizing: border-box;
    border: none;
    border-radius: 4px;
    padding: 0 16px;
    min-width: 64px;
    height: 36px;
    vertical-align: middle;
    text-align: center;
    text-overflow: ellipsis;
    text-transform: uppercase;
    color: rgb(var(--pure-material-onprimary-rgb, 255, 255, 255));
    background-color: #ac3b61;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    font-family: var(--pure-material-font, "Roboto", "Segoe UI", BlinkMacSystemFont, system-ui, -apple-system);
    font-size: 14px;
    font-weight: 500;
    line-height: 36px;
    overflow: hidden;
    outline: none;
    cursor: pointer;
    transition: box-shadow 0.2s;
}

.pure-material-button-contained::-moz-focus-inner {
    border: none;
}

/* Overlay */
.pure-material-button-contained::before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #ac3b61;
    opacity: 0;
    transition: opacity 0.2s;
}

/* Ripple */
.pure-material-button-contained::after {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    border-radius: 50%;
    padding: 50%;
    width: 32px; /* Safari */
    height: 32px; /* Safari */
    background-color: rgb(var(--pure-material-onprimary-rgb, 255, 255, 255));
    opacity: 0;
    transform: translate(-50%, -50%) scale(1);
    transition: opacity 1s, transform 0.5s;
}

/* Hover, Focus */
.pure-material-button-contained:hover,
.pure-material-button-contained:focus {
    box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2), 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12);
}

.pure-material-button-contained:hover::before {
    opacity: 0.08;
}

.pure-material-button-contained:focus::before {
    opacity: 0.24;
}

.pure-material-button-contained:hover:focus::before {
    opacity: 0.3;
}

/* Active */
.pure-material-button-contained:active {
    box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2), 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12);
}

.pure-material-button-contained:active::after {
    opacity: 0.32;
    transform: translate(-50%, -50%) scale(0);
    transition: transform 0s;
}

/* Disabled */
.pure-material-button-contained:disabled {
    color: rgba(var(--pure-material-onsurface-rgb, 0, 0, 0), 0.38);
    background-color: rgba(var(--pure-material-onsurface-rgb, 0, 0, 0), 0.12);
    box-shadow: none;
    cursor: initial;
}

.pure-material-button-contained:disabled::before {
    opacity: 0;
}

.pure-material-button-contained:disabled::after {
    opacity: 0;
}

@-webkit-keyframes animatebottom {
  from { bottom:-100px; opacity:0 } 
  to { bottom:0px; opacity:1 }
}

@keyframes animatebottom { 
  from{ bottom:-100px; opacity:0 } 
  to{ bottom:0; opacity:1 }
}

#myDiv {
  display: none;
  text-align: center;
}

@media screen {
  @media (max-width: 567px) {
    #myProgress {
      position: fixed;
      left: 40%;
      top: 50%;
      border-radius: 25px 25px;
      background-color: #ddd;
    }

    .nav-bar {
      height: 70px;
    }

    .nav-left{
      padding-top: 20px;
      float:left;
    }

    .nav-left img {
      height: 35px
    }

    .nav-right {
      float:right;
    }

    .nav-right li {
      padding: 10px 15px;
      list-style:none;
      font-size:13px;
      color:white;
      width: 10%;
    }

    .nav-right ul {
      list-style-type: none;
      margin: 0px;
      padding: 0px;
      font-family: Verdana, sans-serif;
    }

    body {
      width: 100%;
    }

    .introduction {
      font-size: 18px;
      letter-spacing: 0.3em;
      padding-left: 5%;
      padding-right: 5%;
    }

    .introduction img {
      width: 60%;
    }

    .about {
      padding-left: 5%;
      padding-right: 5%;
      font-size: 12px;
    }

    .skills {
      font-size: 12px;
    }

    .experience {
      font-size: 12px;
      padding-left: 5%;
      padding-right: 5%;
    }

    .content-1 li {
      width: 7%;
    }
  
    .content-2 li {
      width: 15%;
    }
    
  }
}